<template>
<div style="background-color:white">
  <div class="mainArea">
    <!-- 書き込み -->
    <div style="position:relative;">
      <span style="position:absolute; top:-170px; left:0px" id="editParent" />
    </div>
    <div class="sp_line_m" />
    <img :src="this.$root.IMG_PATH + 'icon_' + postTitlePath + '_post.png'" style="width:150px">
    <div class="sp_line_s" />
    <div class="editParent" :class="[this.$root.pageClass['boc'], this.$root.pageClass['bc2']]" style="width:100%; text-align:left">
      <div v-show="this.kbn == 'REPORT'">
        <div style="display:flex; gap: 20px; justify-content:center">
          <label class="ECM_RadioInput"><input class="ECM_RadioInput-Input" type="radio" name="radio" value="0" v-model="reportKbn"><span class="ECM_RadioInput-DummyInput bocReport"></span><span class="ECM_RadioInput-LabelText fcBase">開始</span></label>
          <label class="ECM_RadioInput"><input class="ECM_RadioInput-Input" type="radio" name="radio" value="1" v-model="reportKbn"><span class="ECM_RadioInput-DummyInput bocReport"></span><span class="ECM_RadioInput-LabelText fcBase">終了</span></label>
        </div>
        <div class="dotBar dotBarReport" />
      </div>
      
      <div class="k_mi">
        <span>なまえ</span>
        <div class="constrains">（{{maxLength.nickName}}文字）</div>
      </div>
      <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" :maxlength="maxLength.nickName" style="width:100%;" placeholder="例) まなんだ太郎'"
      v-model="nickName" @change="chValue('NICKNAME')">
      
      <div v-show="this.kbn == 'EXAMINEE'">
        <div class="sp_line_m" />

        <!-- 受験生 -->
        <div class="k_mi">
          <span>都道府県</span>
        </div>
        <select class="form-select" name="series" style="width:50%;" :class="this.$root.pageClass['boc']" v-model="ken">
          <option v-for="(val, index) in this.$root.allKenList" :key="index" :value=index>{{val}}</option>
        </select>

        <div class="sp_line_m" />

        <div class="k_mi">
          <span>志望校&nbsp;/&nbsp;目指している資格・職など</span>
          <div class="constrains">（{{maxLength.choice}}文字）</div>
        </div>
        <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" :maxlength="maxLength.choice" style="width:100%;" placeholder="例）まなんだ大学 司法書士" v-model="choice" @change="chValue('NICKNAME')">
      </div>

      <div v-show="this.kbn != 'REPORT'">
        
        <div class="sp_line_m" />

        <div class="k_mi">
          <span>本文</span>
          <span class="constrains required">*</span>
          <div class="constrains">（{{length("BODY")}}&nbsp;/&nbsp;{{maxBodyLength}}文字）</div>
        </div>
        <textarea class="form-control inputBase" :class="this.$root.pageClass['boc']" rows="7" style="width:100%" v-model="body" :maxlength="maxBodyLength" :placeholder="variableLabel('BODY_PLACEHOLDER')"></textarea>
      </div>
      
      <div v-show="this.kbn == 'REPORT'">
        <div class="sp_line_m" />

        <!-- 勉強報告 -->
        <div class="k_mi">
          <span>勉強内容</span>
          <span class="constrains required">*</span>
          <div class="constrains">（{{maxLength.title}}文字）</div>
        </div>
        <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" :maxlength="maxLength.title" style="display:inline-block; width:100%;" placeholder="例）〇〇大学受験、学校の課題、〇〇資格取得" v-model="title">
        <div class="sp_line_ss" />
        <p style="text-align:right">{{reportTitleLabel}}</p>

        <div class="sp_line_m" />
        
        <div class="k_mi">
          <span>ひとこと</span>
          <div class="constrains">（{{length("MESSAGE")}}&nbsp;/&nbsp;{{maxLength.message}}文字）</div>
        </div>
        <textarea class="form-control inputBase" :class="this.$root.pageClass['boc']" rows="3" style="width:100%" v-model="message" :maxlength="maxLength.message" placeholder="今のお気持ちなどを入力しましょう"></textarea>

        <div v-show="reportKbn == 0">
          <!-- 開始のみ -->
          <div class="sp_line_m" />
          <div id="CHK" class="theme_chk_A1" style="display:flex; justify-content:start">
            <label class="typeA-checkbox">
              <input type="checkbox" v-model="isStartTimer">
              <div class="indicator" :class="this.$root.pageClass['boc']" style="border:1px solid"></div>
            </label>
            <span class="font_m" style="margin-top:7px">書き込みと同時にタイマーをスタート</span>
          </div>
        </div>
        <div v-show="reportKbn == 1">
          <!-- 終了のみ -->
          <div class="sp_line_m" />

          <div class="k_mi">
            <span>勉強時間</span>
          </div>
          <div id="CHK" class="theme_chk_A1" style="display:flex; justify-content:start">
            <label class="typeA-checkbox">
              <input type="checkbox" v-model="isDispTimer">
              <div class="indicator" :class="this.$root.pageClass['boc']" style="border:1px solid"></div>
            </label>
            <span class="font_m" style="margin-top:7px">時間を記載する</span>
          </div>
          
          <div class="sp_line_s" />

          <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" style="width:50px;display:inline; text-align:center" placeholder="00" :maxlength="maxLength.cHour" v-bind:readonly="!isDispTimer"
          v-model="cHour">
          <span style="margin:0 10px">時間</span>
          <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" style="width:50px;display:inline; text-align:center" placeholder="00" :maxlength="maxLength.cMinute" v-bind:readonly="!isDispTimer"
          v-model="cMinute">
          <span style="margin:0 10px">分</span>
          <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" style="width:50px;display:inline; text-align:center" placeholder="00" :maxlength="maxLength.cSecond" v-bind:readonly="!isDispTimer"
          v-model="cSecond">
          <span style="margin-left:10px">秒</span>

          <div class="sp_line_s" />

          <button type="button" class="btnBase" v-on:click="pushFromTimer">タイマーの時間を使う</button>
        </div>
        
      </div>
      <div class="sp_line_m" />

      <div class="k_mi">
        <span>削除番号</span>
      </div>
      <input type="text" class="form-control inputBase" :class="this.$root.pageClass['boc']" :maxlength="maxLength.deleteSecret" style="width:100px;" placeholder="数字5桁まで" v-model="deleteSecret">
      ※削除番号が未入力の場合、投稿を削除することができなくなります

      <div class="sp_line_m" />
      
      <p class="font_l">学位『{{this.$root.degreeLabel(this.$root.degree)}}』で投稿されます。</p>
      変更する場合は画面右上の設定から変更してください。
      
      <div class="dotBar" :class="this.$root.pageClass['db']" />
      ●注意事項<br>
      <ul>
        <li>
          <span class="pointer" :class="this.$root.pageClass['fc']" @click="pushTerms">利用規約</span>をよく読んで内容に同意してから書き込んで下さい。
        </li>
        <li>
          法令または公序良俗に違反する書き込み、卑猥な書き込み、出会いを求める書き込みは禁止していますので絶対にやめて下さい。
        </li>
        <li>
          個人の特定につながる情報の書き込みについては十分ご注意ください。
        </li>
      </ul>
      <div class="sp_line_s" />

      <div id="CHK" class="CHK_TERMS" :class="theme_chk" style="display:flex; justify-content:start">
        <label class="typeA-checkbox">
          <input type="checkbox" v-model="isTerms" true-value="1" false-value="0">
          <div class="indicator" :class="this.$root.pageClass['boc']" style="border:1px solid"></div>
        </label>
        <span style="margin-top:5px"><span class="pointer" :class="this.$root.pageClass['fc']" @click="pushTerms">利用規約</span>に同意しますか？</span>
      </div>

      <div class="sp_line_m" />

      <p class="errMsg" v-if="errorMessage">▼ {{errorMessage}}</p>
      <button v-show="this.isTerms == 0" class="btnBase btnBaseNegative" v-on:click="pushPostBtn">書き込む</button>
      <button v-show="this.isTerms == 1" class="btnBase" v-on:click="pushPostBtn">書き込む</button>
    </div>
  </div>
</div>
<Popup ref="popupEdit" popupKbn="EDIT" @deleteAction="deleteAction" @reportAction="reportAction" @popupAction="popupAction" @reportPopupAction="reportPopupAction"></Popup>
</template>

<script>
import axios from 'axios'
import Popup from './Popup.vue';
export default {
  props: {
    data: Array,
    kbn : String,
    params : Array,
  },
  components: {
    Popup,
  },
  data() {
    return {
      reportKbn : 0,
      nickName : "",
      deleteSecret : "",
      
      //通常
      body : "",
      
      //受験
      ken : 0,
      choice : "",

      //勉強報告
      title : "",
      message : "",
      cHour : "",
      cMinute : "",
      cSecond : "",
      
      //相談レス
      soudanId : "",

      isDispTimer : false,
      isStartTimer : false,
      isTerms : 0,

      errorMessage : "",

      maxLength : {
        nickName : 20,
        deleteSecret : 5,
        body : 400,
        choice : 30,
        title : 50,
        message : 100,
        cHour : 2,
        cMinute : 2,
        cSecond : 2,
      },
      maxBodyLength : 0,
      studyTime : "",
    }
  },
  created : function () {
  },
  computed : {
    reportTitleLabel : function() {
      if (this.reportKbn == 0) {
        return "の勉強をはじめました";
      } else {
        return "の勉強を終了しました";
      }
    },
    postTitlePath : function () {
      if (this.kbn == "REPORT") {return "report";} 
      else if (this.kbn == "EXAMINEE") {return "examinee";}
      else if (this.kbn == "GUCHI") {return "guchi";}
      else if (this.kbn == "SOUDAN") {return "soudan";}
      else if (this.kbn == "RESPONSE") {return "soudan";}
      else if (this.kbn == "NANDEMO") {return "nandemo";}
      else {return ""}
    },
    length : function () {
      return (name) => {
        if (name == "BODY") {
          return this.body.length;
        } else if (name == "TITLE") {
          return this.title.length;
        } else if (name == "MESSAGE") {
          return this.message.length;
        }
      }
    },
    variableLabel : function () {
      return (name) => {
        if (name == "BODY_PLACEHOLDER") {
          if (this.kbn == "EXAMINEE") {
            return "受験に関すること、志望校について、テストの結果、合否報告、ライバルへの激励、同県同士の情報共有などをしましょう";
          } else if (this.kbn == "GUCHI") {
            return "勉強、課題、宿題、学校、家族、友人などで抱える愚痴を吐き出してください";
          } else if (this.kbn == "SOUDAN") {
            return "同年代に聞きたいこと、抱えている悩み、これから決めようとしていることなどについて相談してみましょう";
          } else if (this.kbn == "RESPONSE") {
            return "相談に対する自分の考え、アドバイスなどを入力しましょう";
          } else if (this.kbn == "NANDEMO") {
            return "今日あったこと、おもったことなどなんでも書き込んでみましょう";
          }
        }
        return "";
      }
    }
    // categoryLabel : function () {
    //   let retStr = "";
    //   if (this.data.cat3_name != null) {
    //     if (retStr == "") {
    //       retStr += this.data.cat3_name;
    //     } else {
    //       retStr += " > " + this.data.cat3_name;
    //     }
    //   }
    //   if (this.data.cat2_name != null) {
    //     if (retStr == "") {
    //       retStr += this.data.cat2_name;
    //     } else {
    //       retStr += " > " + this.data.cat2_name;
    //     }
    //   }
    //   if (this.data.cat1_name != null) {
    //     if (retStr == "") {
    //       retStr += this.data.cat1_name;
    //     } else {
    //       retStr += " > " + this.data.cat1_name;
    //     }
    //   }
    //   return retStr;
    // }
  },
  methods : {
    initAction : function () {
      //初期表示処理
      this.$root.editFormAction("INIT");

      //各画面用に値を設定
      this.nickName = this.$root.editFormData.nickName;
      this.deleteSecret = this.$root.editFormData.deleteSecret;
      this.isTerms = 0;

      //最大文字数の設定
      if (this.kbn == "EXAMINEE") {
        this.maxBodyLength = 400;
      } else if (this.kbn == "GUCHI") {
        this.maxBodyLength = 400;
      } else if (this.kbn == "SOUDAN") {
        this.maxBodyLength = 2000;
      } else if (this.kbn == "RESPONSE") {
        this.maxBodyLength = 1000;
      } else if (this.kbn == "NANDEMO") {
        this.maxBodyLength = 400;
      }

      if (this.kbn == "EXAMINEE") {
        this.ken = this.$root.editFormData.ken;
        this.choice = this.$root.editFormData.choice;
      }

      if (this.kbn == "REPORT") {
        this.title = this.$root.editFormData.title;
        this.message = this.$root.editFormData.message;
        this.cHour = "";
        this.cMinute = "";
        this.cSecond = "";
        this.isDispTimer = false;
        this.reportKbn = 0;
      }
      this.body = "";
    },
    responseAction : function (id) {
      this.body = ">>" + id + "\n";
    },
    pushBtn : function (name) {
      if (name == "THREAD") {
        this.$emit("pushThread", "THREAD", this.data.thread_id);
      }
    },
    pushTerms : function () {
      window.open(this.$root.BASE_URL + 'terms');
    },
    pushPostBtn : function () {
      if (this.isTerms == 0) {
        this.errorMessage = "投稿の際は利用規約に同意の上、チェックをつけてください";
        return;
      }

      //入力チェック
      this.nickName = this.nickName.trim();
      if (this.nickName == "") {
        this.errorMessage = "ニックネームが未入力です"; return;
      } else if (this.nickName.length > this.maxLength.nickName) {
        this.errorMessage = "ニックネームは" + this.maxLength.nickName + "文字以下で入力してください"; return;
      }

      this.deleteSecret = this.deleteSecret.trim();
      if (this.deleteSecret.length > this.maxLength.deleteSecret) {
        this.errorMessage = "削除番号は" + this.maxLength.deleteSecret + "文字以下で入力してください"; return;
      }
      
      //区分ごとに入力チェック
      if (this.kbn == "REPORT") {
        //勉強報告
        this.title = this.title.trim();
        if (this.nickName == "") {
          this.errorMessage = "勉強内容が未入力です"; return;
        } else if (this.title.length > this.maxLength.title) {
          this.errorMessage = "勉強内容は" + this.maxLength.title + "文字以下で入力してください"; return;
        }
        
        this.message = this.message.trim();
        if (this.message.length > this.maxLength.message) {
          this.errorMessage = "勉強内容は" + this.maxLength.message + "文字以下で入力してください"; return;
        }

        this.cHour = this.cHour.trim();
        if (this.cHour.length > this.maxLength.cHour) {
          this.errorMessage = "勉強時間（時）は" + this.maxLength.cHour + "文字以下で入力してください"; return;
        }
        
        this.cMinute = this.cMinute.trim();
        if (this.cMinute.length > this.maxLength.cMinute) {
          this.errorMessage = "勉強時間（分）は" + this.maxLength.cMinute + "文字以下で入力してください"; return;
        }
        
        this.cSecond = this.cSecond.trim();
        if (this.cSecond.length > this.maxLength.cSecond) {
          this.errorMessage = "勉強時間（秒）は" + this.maxLength.cSecond + "文字以下で入力してください"; return;
        }
      } else {
        //勉強報告以外
        this.body = this.body.trim();
        if (this.body == "") {
          this.errorMessage = "本文が未入力です"; return;
        } else if (this.body.length > this.maxBodyLength) {
          this.errorMessage = "本文は" + this.maxBodyLength + "文字以下で入力してください"; return;
        }
      }
      
      if (this.kbn == "EXAMINEE") {
        this.choice = this.choice.trim();
        if (this.choice.length > this.maxLength.choice) {
          this.errorMessage = "志望校は" + this.maxLength.choice + "文字以下で入力してください"; return;
        }
      }


      //現在の情報をDBに保存
      let params = new URLSearchParams();
      // params.append('frameIndex', this.frameList[this.selectedFrame]);
      params.append('userId', this.$root.userId);
      params.append('postKbn', this.kbn);
      params.append('nickName', this.nickName);
      params.append('degree', this.$root.degree);
      params.append('secret', this.deleteSecret);

      if (this.kbn != "REPORT") {
        params.append('body', this.body);
      }
      if (this.kbn == "REPORT") {
        params.append('kbn', this.reportKbn);
        params.append('title', this.title);
        params.append('message', this.message);

        this.studyTime = "";
        if (this.reportKbn == 1 && this.isDispTimer) {
          //終了
          if (!isNaN(parseInt(this.cHour)) && parseInt(this.cHour) != 0) {
            this.studyTime += parseInt(this.cHour) + "時間";
          }
          if (!isNaN(parseInt(this.cMinute)) && parseInt(this.cMinute) != 0) {
            this.studyTime += parseInt(this.cMinute) + "分";
          }
          if (!isNaN(parseInt(this.cSecond)) && parseInt(this.cSecond) != 0) {
            this.studyTime += parseInt(this.cSecond) + "秒";
          }

          if (isNaN(parseInt(this.cHour)) && isNaN(parseInt(this.cMinute)) && isNaN(parseInt(this.cSecond))) {
            this.studyTime = "";
          }
        }
        params.append("time", this.studyTime);
      } else if (this.kbn == "EXAMINEE") {
        params.append('ken', this.ken);
        params.append('choice', this.choice);
      } else if (this.kbn == "RESPONSE") {
        params.append('soudanId', this.params.soudanId);
      }

      axios.post(this.$root.SERVER_PATH + "insertPost", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          
          this.$root.chkUserId(resData);

          //フォーム情報の更新
          this.$root.editFormData.nickName = this.nickName;
          this.$root.editFormData.deleteSecret = this.deleteSecret;
          if (this.kbn == "EXAMINEE") {
            this.$root.editFormData.ken = this.ken;
            this.$root.editFormData.choice = this.choice;
          }
          if (this.kbn == "REPORT") {
            this.$root.editFormData.title = this.title;
            this.$root.editFormData.message = this.message;
          }
          this.$root.editFormAction("SAVE");

          this.$refs.popupEdit.title = "";
          this.$refs.popupEdit.body = "投稿が完了しました";
          
          if (this.kbn == "REPORT") {
            if (this.reportKbn == 0) {
              this.$refs.popupEdit.showModal("POST_REPORT_ST_SUCCESS");
            } else {
              this.$refs.popupEdit.showModal("POST_REPORT_ED_SUCCESS");
            }
          } else {
            this.$refs.popupEdit.showModal("POST_SUCCESS");
          }

          this.isTerms = 0;
          if (this.kbn == "SOUDAN") {
            this.$root.autoScroll("TOP");
          } else {
            this.$root.autoScroll("LISTEND");
          }
        }
      });
    },
    popupAction : function (kbn) {
      if (kbn == "POST_SUCCESS" || kbn == "POST_REPORT_ST_SUCCESS" || kbn == "POST_REPORT_ED_SUCCESS") {

        if (kbn == "POST_REPORT_ST_SUCCESS" && this.isStartTimer) {
          //タイマーのスタート
          this.$root.restartTimer();
        }
        
        //呼び出し元の処理
        this.$emit("editAction", {kbn : "POST"});
        this.$refs.popupEdit.closeModal();
      }
    },
    reportPopupAction : function (name) {
      if (name == "ILLUST") {
        //勉強報告 → 共有
        let str = this.title;

        if (this.reportKbn == 0) {
          str += "の勉強を開始しました";
        } else {
          str += "の勉強を終了しました";

          if (this.studyTime != "") {
            str += "（" + this.studyTime + "）";
          }
        }
        
        localStorage.setItem("ILLUST_BODY", str);
        this.$router.push("/illust");
      }
    },
    pushFromTimer : function () {
      this.isDispTimer = true;
      var hours = Math.floor(this.$root.timerValue / 3600);
      var minutes = Math.floor((this.$root.timerValue % 3600) / 60);
      var remainingSeconds = this.$root.timerValue % 60;

      this.cHour = hours.toString().padStart(2, '0');
      this.cMinute = minutes.toString().padStart(2, '0');
      this.cSecond = remainingSeconds.toString().padStart(2, '0');
    },
    pushRowBtn : function (name, val) {
       if (name == "DELETE") {
        this.targetReportId = val;
        //ポップアップ
        this.$refs.popup.deleteNo = "";
        this.$refs.popup.title = "";
        this.$refs.popup.showModal("DELETE");
      } else if (name == "REPORT") {
        this.targetReportId = val;
        //ポップアップ
        this.$refs.popup.reportMessage = "";
        this.$refs.popup.title = "";
        this.$refs.popup.showModal("REPORT");
      }
    },
    chTitle : function () {
      localStorage.setItem("TITLE_NAME", this.titleName);
    },
    deleteAction : function (val) {
      let params = new URLSearchParams();
      params.append('reportId', this.targetReportId);
      params.append('deleteNo', val);
      axios.post(this.$root.SERVER_PATH + "deleteReport", params)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          this.$refs.popup.errorMessage = "暗証番号が正しくありません";
        } else if (resData["status"] == this.$root.API_SUCCESS) {
          this.$refs.popup.title = "";
          this.$refs.popup.body = "";
          this.$refs.popup.showModal("DELETE_SUCCESS");
        }
        //  else if (resData["status"] == this.$root.API_SUCCESS) {

        // }
      });
    },
    reportAction : function (val) {
      let params = new URLSearchParams();
      params.append('reportId', this.targetReportId);
      params.append('userId', this.userId);
      params.append('message', val);
      axios.post(this.$root.SERVER_PATH + "reportReport", params)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_SUCCESS) {
          this.$refs.popup.title = "";
          this.$refs.popup.body = "";
          this.$refs.popup.showModal("REPORT_SUCCESS");
        }
      });
    },
  },
  watch : {
  }


}
</script>

