<template>
  <div class="Top2 bcYellow" style="min-height:100vh">
    <div style="height:200px" />
    <div class="mainArea">
      <img :src="this.$root.IMG_PATH + 'pagetitle_report.png'" style="width:60%; max-width:300px">

      <div class="sp_line_s" />
      <div class="pageDescription bocReport">
        勉強の開始と終了をみんなに報告していきましょう。<br>
        やる気がない時でも他の人も勉強していると思うとモチベーションも上がるはずです。<br>
        逆に自分が勉強したときは他の人にがんばった自慢をしていきましょう！<br>
        受験勉強、学校の宿題、資格取得の勉強なんでもOKです。
      </div>
      <div class="sp_line_s" />

      <button type="button" class="btnBase writeBtn" @click="this.$root.autoScroll('EDIT')">書き込む</button>

      <div class="dotBar dotBarReport" />

      <ListFrame kbn="REPORT" :list="this.postList" @rowAction="rowAction"></ListFrame>
      
      <div class="dotBar dotBarReport" />

      <subPagenation
        :showPages="showPages"
        :currentPage="currentPage"
        :totalCount="totalCount"
        :perPage="perPage"
        :totalPages="totalPages"
        @currentPage="getCurrentPage"
      ></subPagenation>
    </div>
  </div>
  <EditFrame ref="edit" kbn="REPORT" @editAction="editAction"></EditFrame>

  <popup-window ref="popup" @deleteAction="deleteAction" @reportAction="reportAction" @popupAction="popupAction"></popup-window>
</template>

<script>
import axios from 'axios'
import SubPagenation from './SubPagenation.vue';
import EditFrame from './EditFrame.vue';
import ListFrame from './ListFrame.vue';

export default {
  name: 'Report',
  components: {
    SubPagenation,
    EditFrame,
    ListFrame
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 20, //1ページの表示件数
      totalCount: 0, //取得したアイテムの総件数
      totalPages: 0, //総ページ数

      keyword : "",
      errMsg : "",
      postList : [],

      targetReportId : "",
      cardBaseScale : 1.0,

    }
  },
  created : function () {
  },
  computed : {
    cardTimeLabel : function () {
      var formattedTime = '';
      if (this.cHour != "" && parseInt(this.cHour) != 0) {
        if (isNaN(parseInt(this.cHour))) {
          formattedTime += '--時間';
        } else {
          formattedTime += parseInt(this.cHour) + '時間';
        }
      }
      if (this.cMinute != "" && parseInt(this.cMinute) != 0) {
        if (isNaN(parseInt(this.cMinute))) {
          formattedTime += '--分';
        } else {
          formattedTime += parseInt(this.cMinute) + '分';
        }
      }
      if (this.cSecond != "" && parseInt(this.cSecond) != 0) {
        if (isNaN(parseInt(this.cSecond))) {
          formattedTime += '--秒';
        } else {
          formattedTime += parseInt(this.cSecond) + '秒';
        }
      }
      
      if (parseInt(this.cHour) == 0 && parseInt(this.cMinute) == 0 && parseInt(this.cSecond) == 0) {
        formattedTime = "";
      }

      return formattedTime;
    },
    frameKbn : function () {
      return (frameName) => {
        if (frameName.indexOf("ba", 0) != -1) {
          return "demoBa";
        } else if (frameName.indexOf("sc", 0) != -1) {
          return "demoSc";
        }
      }
    },
    selFrame : function () {
      return (index) => {
        if (index == this.selectedFrame) {
          return "selectFrame";
        } else {
          return "";
        }
      }
    },
    cardFont : function () {
      return (kbn) => {
        let cardWidth = 600;
        if (kbn == 0) {
          //タイトル
          let tLength = 0;
          for (let i = 0; i < this.titleName.length; i++) {
            const charCode = this.titleName.charCodeAt(i);
            if ((charCode >= 0x0020 && charCode <= 0x007E) || (charCode >= 0xFF61 && charCode <= 0xFF9F)) {
              tLength += 1; // 半角文字
            } else {
              tLength += 2; // 全角文字
            }
          }

          let fSize = 40;
          if (fSize > (cardWidth * 0.75 / tLength) * 2) {
            fSize = (cardWidth * 0.75 / tLength) * 2;
            if (fSize < 25) {
              //2行にする
              return "font-size: 25px; white-space: normal; width: " + (cardWidth * 0.75) + "px";
            }
          }
          
          return "font-size:" + fSize + "px; white-space: nowrap;";
        } else if (kbn == 1) {
          //ラベル
          return "font-size:" + "20" + "px";
        }
      }
    },
  },
  mounted () {
    console.log("mounted");

    this.$nextTick(function() {
      //タイトル
      this.titleName = localStorage.getItem("TITLE_NAME") || "";
     
      // // eslint-disable-next-line
      // postscribe('#data-script', `<div><script type="text/javascript"> var _adf_global = {base: {}}; _adf_global.base.ad_id = "647e57ee38396d73e85f9099";_adf_global.base.iframe_width = 320; _adf_global.base.iframe_height = 50;  _adf_global.base.reload_span_sec = 30; _adf_global.base.disable_reload = false; <\/script><script type="text/javascript" src="//d1bqhgjuxdf1ml.cloudfront.net/js/adf_global_base_v1.2.min.js" charset="UTF-8"><\/script><\/div>
      // `, {
      //     done: function() {
      //         console.log("OK");
      //     }
      // })
    });
  },
  methods : {
    handleKeydown(e) {
      // this.chValue('OUTLINE')
      if (this.composing) {
        return;
      } // IME対応(日本語変換中のEnterを止める)
      if (e.key === 'Enter') {
        this.pushBtn("SEARCH");
      }
    },
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    pushBtn : function (name) {
      if (name == "SEARCH") {
        let query = Object.assign({}, this.$route.query);

        //キーワードが同じ場合 リロード
        if (query["keyword"] == this.keyword &&
          query["page"] == 1
        ) {
          this.$root.showLoad();
          this.getPostList();
          return;
        }

        query["keyword"] = this.keyword;
        query["page"] = 1;

        this.$router.push({query});
      }
    },
    editAction : function (params) {
      if (params.kbn == "POST") {
        //投稿成功
        let query = Object.assign({}, this.$route.query);
        if (query["page"] == 1) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        this.$router.push({query});
      }
    },
    rowAction : function (params) {
      if (params.kbn == "STAMP") {
        let id = params.id;

        for (let i = 0; i < this.postList.length; i++) {
          if (this.postList[i].id == id) {
            if (this.postList[i].stamp_count == null) {
              this.postList[i].stamp_count = 1;
            } else {
              this.postList[i].stamp_count = parseInt(this.postList[i].stamp_count) + 1;
            }
            this.postList[i].my_flg = "A";
          }
        }
      } else if (params.kbn == "DELETE") {
        let id = params.id;

        for (let i = 0; i < this.postList.length; i++) {
          if (this.postList[i].id == id) {
            this.postList.splice(i, 1);
            return;
          }
        }
      }
    },
    getPostList : function () {
      this.$root.pageAction("REPORT");

      let params = new URLSearchParams();
      params.append('postKbn', "REPORT");
      params.append('page', this.currentPage);
      params.append('userId', this.$root.userId);
      params.append('degree', this.$root.retDegreeParam());

      axios.post(this.$root.SERVER_PATH + "getPostList", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          this.postList = resData["postList"];
          this.totalCount = resData["postCount"];

          var tPage = Math.floor(this.totalCount / this.perPage);
          if (this.totalCount % this.perPage != 0) {
            tPage = tPage + 1;
          }
          this.totalPages = parseInt(tPage);

          let reportInit = localStorage.getItem("REPORT_INIT");
          if (reportInit == null) {
            reportInit = "";
          }

          this.$nextTick(function() {
            if (reportInit == "START" || reportInit == "END") {
              this.$root.autoScroll("EDIT");
              localStorage.removeItem("REPORT_INIT");
            }
          });
        }
      });
    },
    beforeAction : function() {
      let query = Object.assign({}, this.$route.query);
      let okFlg = true;

      var qPage = query["page"];
      if (qPage == null || isNaN(parseInt(qPage)) || parseInt(qPage) < 1) {
        qPage = 1;
        okFlg = false;
      }
      
      if (okFlg == false) {
        //この時点でアウトの場合
        query["page"] = qPage;
        this.$router.replace({query});
      } else {
        this.currentPage = parseInt(qPage);
        this.$refs.edit.initAction();
        this.getPostList();
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  watch : {
    '$route'(to) {
      if (to["name"] == "Report") {
        this.$root.showLoad();
        this.beforeAction();
      }
    }
  }


}
</script>

<style>
.st0{fill:#51BFE2;}
</style>