          <img :src="this.$root.IMG_PATH + 'timer_dot.png'" :style="'width:' + this.timerWidth / 2 + 'px'">
          <div class="num_com" :class="'num_' + timerValues[2]" :style="styles"></div>
          <div class="num_com" :class="'num_' + timerValues[3]" :style="styles"></div>
          <img :src="this.$root.IMG_PATH + 'timer_dot.png'" :style="'width:' + this.timerWidth / 2 + 'px'">
          <div class="num_com" :class="'num_' + timerValues[4]" :style="styles"></div>
