        </div> -->

        <div style="display:flex; gap:10px; justify-content:center; align-items:center">
          <img :src="this.$root.IMG_PATH + 'btn_timer_stop.png'" style="width:40px;" v-on:click="pushStatus('STOP')">
          <img v-show="this.$root.status == 'INIT' " style="width:70px" :src="this.$root.IMG_PATH + 'btn_timer_play.png'" v-on:click="pushStatus('START')">
          <img v-show="this.$root.status == 'PAUSE'" style="width:70px" :src="this.$root.IMG_PATH + 'btn_timer_play.png'" v-on:click="pushStatus('RESUME')">
          <img v-show="this.$root.status == 'START'" style="width:70px" :src="this.$root.IMG_PATH + 'btn_timer_pause.png'" v-on:click="pushStatus('PAUSE')">
          <img v-show="this.$root.status == 'STOP'" style="width:70px" :src="this.$root.IMG_PATH + 'btn_timer_reset.png'" v-on:click="pushStatus('RESET')">
          <img :src="this.$root.IMG_PATH + 'btn_timer_report.png'" style="width:40px" v-on:click="pushStatus('REPORT_START')">
          <!-- <div  class="timerBtn" :class="btnProperty(0)" v-on:click="pushStatus('START')" />
          <div v-show="this.$root.status == 'PAUSE'" class="timerBtn link tb_play"  v-on:click="pushStatus('RESUME')" />
          <div class="timerBtn" :class="btnProperty(1)" style="width:20px" v-on:click="pushStatus('PAUSE')" />
          <div class="timerBtn" :class="btnProperty(2)" v-on:click="pushStatus('STOP')" />
          <div class="timerBtn" :class="btnProperty(3)" v-on:click="pushStatus('RESET')" /> -->
        </div>
        
        <div class="sp_line_ss" />

        <div v-show="this.$root.status == 'INIT'">
          <span class="font_xl fcMain">スタートボタンを押して勉強を開始してください。<br>
          先に開始報告する場合は吹き出しボタンを押してください。</span>
        </div>
        <div v-show="this.$root.status == 'START'">
          <img :src="this.$root.IMG_PATH + 'timer_label1.png'" style="width:40%; max-width:300px">
        </div>
        <div v-show="this.$root.status == 'PAUSE'">
          <img :src="this.$root.IMG_PATH + 'timer_label2.png'" style="width:40%; max-width:300px">
        </div>
        <div v-show="this.$root.status == 'STOP'">
          <img :src="this.$root.IMG_PATH + 'timer_label3.png'" style="width:40%; max-width:300px"><br>
          <span class="font_xl fcMain">終了報告する場合は吹き出しボタンを押してください。</span>
        </div>
        <div class="sp_line_m" />
      </div>
    </div>
    
    <div class="mainArea">
      <div class="sp_line_m" />

      <!-- 各種新着一覧 -->
      <img :src="this.$root.IMG_PATH + 'pagetitle_info.png'" style="width:60%; max-width:300px">
      <div class="sp_line_s" />
      <div class="pageDescription bocMain" style="width:90%">
        2023/12/1 「まなぴよ」サービス開始
      </div>

      <div class="sp_line_m" />

      <!-- 各種新着一覧 -->
      <img :src="this.$root.IMG_PATH + 'pagetitle_topB.png'" style="width:60%; max-width:300px">
      
      <div class="sp_line_m" />
      <img :src="this.$root.IMG_PATH + 'pagetitle_report.png'" style="width:40%; max-width:200px">
      <ListFrame kbn="REPORT" isLatest="true" :list="this.reportList" @rowAction="rowAction"></ListFrame>
      <div v-show="this.reportList.length != 0">
        <div class="sp_line_ss" />
        <button type="button" class="btnBase" @click="pushMore(0)">もっと見る</button>
      </div>
      <div class="sp_line_l" />

      <img :src="this.$root.IMG_PATH + 'pagetitle_examinee.png'" style="width:40%; max-width:200px">
      <ListFrame kbn="EXAMINEE" isLatest="true" :list="this.examineeList" @rowAction="rowAction"></ListFrame>
      <div v-show="this.examineeList.length != 0">
        <div class="sp_line_ss" />
        <button type="button" class="btnBase" @click="pushMore(1)">もっと見る</button>
      </div>
      <div class="sp_line_l" />

      <img :src="this.$root.IMG_PATH + 'pagetitle_guchi.png'" style="width:40%; max-width:200px">
      <ListFrame kbn="GUCHI" isLatest="true" :list="this.guchiList" @rowAction="rowAction"></ListFrame>
      <div v-show="this.guchiList.length != 0">
        <div class="sp_line_ss" />
        <button type="button" class="btnBase" @click="pushMore(2)">もっと見る</button>
      </div>
      <div class="sp_line_l" />
      
      <img :src="this.$root.IMG_PATH + 'pagetitle_soudan.png'" style="width:40%; max-width:200px">
      <ListFrame kbn="SOUDAN" isLatest="true" :list="this.soudanList" @rowAction="rowAction"></ListFrame>
      <div v-show="this.soudanList.length != 0">
        <div class="sp_line_ss" />
        <button type="button" class="btnBase" @click="pushMore(3)">もっと見る</button>
      </div>
      <div class="sp_line_l" />

      <img :src="this.$root.IMG_PATH + 'pagetitle_nandemo.png'" style="width:40%; max-width:200px">
      <ListFrame kbn="NANDEMO" isLatest="true" :list="this.nandemoList" @rowAction="rowAction"></ListFrame>
      <div v-show="this.nandemoList.length != 0">
        <div class="sp_line_ss" />
        <button type="button" class="btnBase" @click="pushMore(4)">もっと見る</button>
      </div>
      <div class="sp_line_m" />

      <div class="sp_line_m" />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ListFrame from './ListFrame.vue';
export default {
  name: 'Top2',
  components: {
    ListFrame
  },
  data() {
    return {
      // timerWidth : 38,
      // selectedFrame : 0,
      // isSelectFrame : false,

      // labelArray : ["", "の勉強を始めました"],
      // labelArray1 : ["img_make_start.png", "の勉強を始めました"],
      // labelArray2 : ["img_make_stop.png", "の勉強を終了しました"],
 
      // isHowto : false,

      reportList : [],
      examineeList : [],
      guchiList : [],
      soudanList : [],
      nandemoList : [],
    }
  },
  created : function () {
  },
  computed : {
    styles() {
      return {
        '--timerWidth': this.timerWidth + "px",
        '--timerAllWidth': this.timerWidth * 10 + "px",
        '--timerHeight': this.timerWidth / 38 * 50 + "px",
        '--timerNum0' : this.timerWidth * 0 + "px",
        '--timerNum1' : this.timerWidth * -1 + "px",
        '--timerNum2' : this.timerWidth * -2 + "px",
        '--timerNum3' : this.timerWidth * -3 + "px",
        '--timerNum4' : this.timerWidth * -4 + "px",
        '--timerNum5' : this.timerWidth * -5 + "px",
        '--timerNum6' : this.timerWidth * -6 + "px",
        '--timerNum7' : this.timerWidth * -7 + "px",
        '--timerNum8' : this.timerWidth * -8 + "px",
        '--timerNum9' : this.timerWidth * -9 + "px",
      };
    },
    btnProperty : function () {
      return (index) => {
        let retVal = "";
        if (index == 0) {
          //再生ボタン
          if (this.$root.status == "START") {
            retVal = "tb_play_on";
          } else if (this.$root.status == "STOP") {
            retVal = "tb_play_off";
          } else {
            retVal = "tb_play link";
          }
        } else if (index == 1) {
          //一時停止ボタン
          if (this.$root.status == "PAUSE") {
            retVal = "tb_pause_on";
          } else if (this.$root.status == "INIT" || this.$root.status == "STOP") {
            retVal = "tb_pause_off";
          } else {
            retVal = "tb_pause link";
          }
        } else if (index == 2) {
          //停止ボタン
          if (this.$root.status == "STOP") {
            retVal = "tb_stop_on";
          } else if (this.$root.status == "INIT") {
            retVal = "tb_stop_off";
          } else {
            retVal = "tb_stop link";
          }
        } else if (index == 3) {
          //リセットボタン
          if (this.$root.status == "INIT" || this.$root.status == "START" || this.$root.status == "PAUSE") {
            retVal = "tb_reset_off";
          } else {
            retVal = "tb_reset link";
          }
        }
        
        return retVal;
      }
    },
    isImg : function () {
      return (frameName, kbn) => {
        if (frameName == null) {
          return false;
        }
        if (frameName.indexOf("#") != -1) {
          if (kbn == 0) {
            return false;
          } else {
            return 'inline-block';
          }
        } else {
          if (kbn == 0) {
            return true;
          } else {
            return 'none';
          }
        }
      }
    },
    timerWidth : function() {
      console.log(this.$root.baseParentWidth);
      var pWidth = this.$root.baseParentWidth;
      let retVal = 38;
      if (pWidth > 550) {
        retVal = 55;
      } else {
        retVal = this.$root.baseParentWidth / 10;
      }
      // if (retVal > 60) {
      //   retVal = 60;
      // }
      return retVal;
    },
    timerFontSize : function () {
      let fSize = this.$root.baseParentWidth / 6.5;
      if (fSize > 90) {
        fSize = 90;
      }
      return "font-size:" + fSize + "px";
    },
    timerValues : function () {
      var hours = Math.floor(this.$root.timerValue / 3600);
      var minutes = Math.floor((this.$root.timerValue % 3600) / 60);
      var remainingSeconds = this.$root.timerValue % 60;

      var h1 = parseInt(hours / 10);
      var h2 = parseInt(hours % 10);
      
      var m1 = parseInt(minutes / 10);
      var m2 = parseInt(minutes % 10);
      
      var s1 = parseInt(remainingSeconds / 10);
      var s2 = parseInt(remainingSeconds % 10);
      return [h1, h2, m1, m2, s1, s2];
    },
    timerLabel : function () {
      var hours = Math.floor(this.$root.timerValue / 3600);
      var minutes = Math.floor((this.$root.timerValue % 3600) / 60);
      var remainingSeconds = this.$root.timerValue % 60;

      var formattedTime = '';
      formattedTime += hours.toString().padStart(2, '0') + ':';
      formattedTime += minutes.toString().padStart(2, '0') + ':';
      formattedTime += remainingSeconds.toString().padStart(2, '0');
      return formattedTime;
    },
  },
  mounted () {
  },
  methods : {
    pushHowto : function () {
      this.isHowto = !this.isHowto;
    },
    pushMore : function (index) {
      if (index == 0) {
        this.$router.push("/report");
      } else if (index == 1) {
        this.$router.push("/examinee");
      } else if (index == 2) {
        this.$router.push("/guchi");
      } else if (index == 3) {
        this.$router.push("/soudan");
      } else if (index == 4) {
        this.$router.push("/nandemo");
      }
    },
    chFrame : function (index) {
      this.selectedFrame = index;
      this.isSelectFrame = false;
      localStorage.setItem("SEL_FRAME", this.frameList[this.selectedFrame]);
    },
    pushStatus : function (val) {
      if (this.$root.status == val) {
        //現在と同じ場合
        return;
      } else if (this.$root.status == "INIT" && (val == "PAUSE" || val == "STOP" || val == "RESET")) {
        return;
      } else if (this.$root.status == "START" && (val == "RESET")) {
        return;
      } else if (this.$root.status == "PAUSE" && (val == "RESET")) {
        return;
      } else if (this.$root.status == "STOP" && (val == "START" || val == "PAUSE")) {
        return;
      }

      if (val == "REPORT_START" || val == "REPORT_END") {
        //勉強報告へ
        if (val == "REPORT_START") {
          localStorage.setItem("REPORT_INIT", "START");
        } else if (val == "REPORT_END") {
          localStorage.setItem("REPORT_INIT", "END");
        }
        this.$router.push("/report");
        return;
      }

      this.$root.status = val;
      // this.labelArray = this.labelArray1;

      if (val == "RESET") {
        //リセット
        this.$root.timerValue = 0;
        this.$root.currentTime = 0;
        this.$root.startTime = "";
        this.$root.status = "INIT";
        localStorage.removeItem("START_TIME");
        localStorage.removeItem("CURRENT_SEC");
      } else if (val == "START") {
        //開始
        this.$root.calculateWindowWidth();
        this.$root.startTime = new Date();
        this.$root.currentTime = 0;
        this.$root.timerValue = 0;
        localStorage.setItem("START_TIME", this.$root.startTime.toLocaleString());
        localStorage.setItem("CURRENT_SEC", 0);
      } else if (val == "STOP") {
        //終了
        this.$root.currentTime = this.$root.timerValue;
        this.$root.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.$root.currentTime);
        localStorage.removeItem("START_TIME");
        // this.labelArray = this.labelArray2;

        this.cHour = Math.floor(this.$root.currentTime / 3600);
        this.cMinute = Math.floor((this.$root.currentTime % 3600) / 60);
        this.cSecond = this.$root.currentTime % 60;
      } else if (val == "PAUSE") {
        //一時停止
        //これまでの秒数を保管
        let addVal = Math.floor((new Date() - this.$root.startTime) / 1000);
        this.$root.currentTime += addVal;
        this.$root.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.$root.currentTime);
        localStorage.removeItem("START_TIME");
      } else if (val == "RESUME") {
        //再開
        this.$root.status = "START";
        this.$root.startTime = new Date();
        localStorage.setItem("START_TIME", this.$root.startTime.toLocaleString());
      }
      localStorage.setItem("STATUS", this.$root.status);
    },
    initAction : function () {
      this.$root.pageAction("TOP");
      
      let params = new URLSearchParams();
      params.append('page', "-1");
      params.append('userId', this.$root.userId);
      params.append('degree', this.$root.retDegreeParam());

      axios.post(this.$root.SERVER_PATH + "getLatestPostList", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          // this.postList = resData["postList"];

          this.reportList = resData["reportList"];
          this.examineeList = resData["examineeList"];
          this.guchiList = resData["guchiList"];
          this.soudanList = resData["soudanList"];
          this.nandemoList = resData["nandemoList"];
          
          this.$root.chkUserId(resData);
        }
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }


}
</script>

