<template>
  <div class="sp_line_l" />
  <div v-if="data != null" class="bcWhite" :class="pageClass['boc']" style="position:relative; text-align:left; width:97%; margin-right:0; margin-left:auto; padding:10px; border-radius:6px; border: 2px solid">
    <p class="font_l fcWhite" :class="pageClass['bc']" style="position:absolute; min-width:250px; max-width:400px; left:-10px; top:-20px; padding:5px 20px 5px 10px; border-radius:0 30px 15px 0">
      <img :src="this.$root.IMG_PATH + 'icon_row_pen.png'" style="height:24px">
      {{nameLabel}}
    </p>
    <!-- スタンプ -->
    <img v-show="kbn != 'SOUDAN' && !isLatest && !isRes" :src="this.$root.IMG_PATH + 'icon_stamp_' + stampBtnPath(data) + '.png'" style="position:absolute; width:110px; top:-30px; right:0px" class="pointer" v-on:click="pushRowBtn('STAMP', data)">
    <p v-show="kbn != 'SOUDAN' && !isLatest && !isRes" class="font_xl pointer" :class="stampClass" style="position:absolute; top: -31px; right: 0px; width:50px; text-align:center"  v-on:click="pushRowBtn('STAMP', data)">{{data.stamp_count ? data.stamp_count : 0}}</p>
      <!-- <img v-show="data.my_flg == null" :src="this.$root.IMG_PATH + 'btn_stamp_' + stampBtnPath(data) + '_on.png'" style="height:20px" v-on:click="pushRowBtn('STAMP', data)" class="link">
      <img v-show="data.my_flg != null" :src="this.$root.IMG_PATH + 'btn_stamp_' + stampBtnPath(data) + '_off.png'" style="height:20px"> -->
    <div class="sp_line_s" />
    <!-- 勉強報告 ひとこと -->
    <div v-show="this.kbn == 'REPORT'">
      <p class="font_l fcReport">{{reportLabel}}</p>
      <div v-show="data.message != ''">
        <div class="sp_line_ss" />
        <p class="font_m bocReport" style="width:100%; border:2px solid; border-radius:6px; padding:10px; white-space: pre-wrap;">{{data.message}}</p>
      </div>
      <div class="sp_line_ss" />
    </div>

    <!-- 受験生 志望校 -->
    <div v-show="this.kbn == 'EXAMINEE' && data.choice != ''">
      <p class="font_l fcExaminee" style="">志望校：{{data.choice}}!</p>
      <div class="sp_line_ss" />
    </div>

    <div v-show="this.kbn != 'REPORT'">
      <div :class="pageClass['boc']" style="padding:10px; width:100%; border:2px solid; border-radius:6px; white-space: pre-wrap;">
        <p :id="'rowBody' + kbn + rowIndex" class="font_m" v-html="alignStr(data.body)" @click="pushBody" :style="rowBodyStyle"></p>
        <div v-show="isOpenBtn(data)">
          <div class="sp_line_ss" />
          <span v-show="data.isOpen == null || data.isOpen == 0" class="pointer" :class="this.pageClass['fc']" v-on:click="pushOpenBody(data, true)">すべて見る</span>
          <span v-show="data.isOpen == 1" class="link" v-on:click="pushOpenBody(data, false)">一部表示</span>
        </div>
      </div>
      <div class="sp_line_ss" />
    </div>

    <!-- 下段 -->
    <div style="display:flex; justify-content:space-between; align-items:start">
      <div style="text-align:right">
        <span class="fcWhite" style="background-color:#6F6F6F; padding: 2px 5px">{{ this.$root.degreeLabel(data.degree)}}</span>
        &nbsp;{{this.$root.userIdLabel(data.user_id)}}
        <span v-show="!isLatest && !isRes">
          <span :class="linkClass" v-on:click="pushRowBtn('REPORT', data)">[通報]</span>
          <span v-show="this.$root.userId == data.user_id && data.secret == 'del'">&nbsp;&nbsp;
            <span :class="linkClass" v-on:click="pushRowBtn('DELETE', data)">[削除]</span>
          </span>
        </span>
      </div>
      <span>{{ alignDate(data.created_at) }}&nbsp;<span :class="linkClass" v-on:click="pushRowBtn('NO', data)">[{{data.id}}]</span></span>
    </div>
    <!-- 相談 返信 -->
    <div v-show="kbn == 'SOUDAN' && rowIndex != -1" style="text-align:right">
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushRowBtn('RESPONSE', data)">返信を見る（{{data.res_count == null ? 0 : data.res_count}}）</button>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    kbn : String,
    isLatest : Boolean,
    data: { type:Array, default: () => {}},
    isRes : Boolean,
    rowIndex : Number,
  },
  components: {
  },
  data() {
    return {
      targetId : -1,
      isOver : false,
    }
  },
  created : function () {
  },
  mounted : function () {
    if (this.data == null) {
      return;
    }
    this.alignBody();
  },
  computed : {
    stampClass : function () {
      if (this.data.my_flg == null) {
        return "";
      } else {
        return "fcPink";
      }
    },
    pageClass : function() {
      let list = {};
      if (this.kbn == "REPORT") {
        list = {fc: "fcReport", bc: "bcReport", boc: "bocReport"};
      } else if (this.kbn == "EXAMINEE") {
        list = {fc: "fcExaminee", bc: "bcExaminee", boc: "bocExaminee"};
      } else if (this.kbn == "GUCHI") {
        list = {fc: "fcGuchi", bc: "bcGuchi", boc: "bocGuchi"};
      } else if (this.kbn == "SOUDAN" || this.kbn == "RESPONSE") {
        list = {fc: "fcSoudan", bc: "bcSoudan", boc: "bocSoudan"};
      } else if (this.kbn == "NANDEMO") {
        list = {fc: "fcNandemo", bc: "bcNandemo", boc: "bocNandemo"};
      }
      return list;
    },
    rowBodyStyle : function () {
      if (this.isOver && (this.data.isOpen == null || this.data.isOpen == 0)) {
        return "height:200px; overflow-y:hidden";
      } else {
        return "";
      }
    },
    isOpenBtn : function () {
      return (data) => {
        if (data.body == null) {
          return false;
        }

        if (this.isOver) {
          return true;
        } else {
          return false;
        }
      }
    },
    stampBtnPath : function () {
      return (data) => {
        if (this.kbn == "REPORT" && data.kbn == "0") {return "report1";} 
        else if (this.kbn == "REPORT" && data.kbn == "1") {return "report2";} 
        else if (this.kbn == "EXAMINEE") {return "examinee";}
        else if (this.kbn == "GUCHI") {return "guchi";}
        else if (this.kbn == "SOUDAN") {return "soudan";}
        else if (this.kbn == "RESPONSE") {return "soudan";}
        else if (this.kbn == "NANDEMO") {return "nandemo";}
        else {return ""}
      }
    },
    alignDate : function () {
      return (dateStr) => {
        var date = new Date(dateStr);
				var year = date.getFullYear();
				var month = date.getMonth() + 1;
				var day = date.getDate();
				var hour = date.getHours();
				var min = date.getMinutes();

        var now = new Date();
        var nYear = now.getFullYear();

        if (year == nYear) {
          //同じ年
          // return ('0' + month).slice(-2) + "/" + ('0' + day).slice(-2) + " " + ('0' + hour).slice(-2) + ":" + ('0' + min).slice(-2);
          return month + "月" + day + "日 " + ('0' + hour).slice(-2) + ":" + ('0' + min).slice(-2);
        } else {
          //違う年
          // return year + "/" +('0' + month).slice(-2) + "/" + ('0' + day).slice(-2) + " " + ('0' + hour).slice(-2) + ":" + ('0' + min).slice(-2);
          return year + "年" + month + "月" + day + "日 " + ('0' + hour).slice(-2) + ":" + ('0' + min).slice(-2);
        }
      }
    },
    alignStr : function () {
      return (bodyStr) => {
        let str = bodyStr;
        if (str == null || str == "") {
          return "";
        } else if (this.isLatest) {
          return str;
        }
        let strArray = str.split('');

        let retStr = "";
        for (let i = 0; i < strArray.length; i++) {
          if (strArray[i] == ">") {
            //次の文字も>か
            if (i != strArray.length - 1 && strArray[i + 1] == ">") {
              //数字が終わるまで
              let numStr = "";
              let j = i + 2;
              for (j; j < strArray.length; j++) {
                if (/^\d$/.test(strArray[j])) {
                  numStr += strArray[j];
                } else {
                  break;
                }
              }
              let num = parseInt(numStr, 10);
              if (!isNaN(num) && num >= 0) {
                //追加
                retStr += "<span id='res' data-param='" + numStr + "' class='link'>&gt;&gt;" + numStr + "</span>"
                i = j - 1;
              } else {
                retStr += strArray[i];
              }
            } else {
              retStr += strArray[i];
            } 
          } else {
            retStr += strArray[i];
          }
        }

        return retStr;
      }
    },
    linkClass : function () {
      if (this.kbn == "SOUDAN" || this.kbn == "REPORT" || this.isLatest || this.isRes) {
        return "";
      }
      return "pointer " + this.pageClass['fc'];
    },
    reportLabel : function () {
      let retVal = "";
      if (this.data.kbn == 0) {
        //開始
        retVal = "『" + this.data.title + "』の勉強をはじめました。";
      } else if (this.data.kbn == 1) {
        let str = "『" + this.data.title + "』の勉強をおわりました";
        if (this.data.time != "") {
          str += "（" + this.data.time + "）";
        }
        retVal = str;
      }
      return retVal;
    },
    nameLabel : function () {
      if (this.data.degree == 99) {
        return this.data.name;
      }
      
      let retVal = this.data.name + "さん";

      if (this.kbn == "EXAMINEE") {
        if (parseInt(this.data.ken) != 0) {
          retVal = retVal + "（" + this.$root.kenLabel(parseInt(this.data.ken)) + "）";
        }
      }
      return retVal;
    },
  },
  methods : {
    alignBody : function () {
      this.$nextTick(function() {
        let bodyHeight = document.getElementById('rowBody' + this.kbn + this.rowIndex).clientHeight;

        if (bodyHeight > 200) {
          this.isOver = true;
        }
      });
    },
    pushOpenBody : function (data, flg) {
      if (flg) {
        data.isOpen = 1;
      } else {
        data.isOpen = 0;
      }
    },
    pushBody : function (event) {
      let target = event.target;

      if (target.id == "res" && !isNaN(parseInt(target.getAttribute('data-param')))) {
        //返信を表示
        this.$emit('pushResponse', target.getAttribute('data-param'));
      }
    },
    pushRowBtn : function (name, data) {
      this.$emit('pushRowBtn', name, data.id);
    },
  },
  watch : {
  }


}
</script>

